<template>
 <div class="create-page">
     <div class="create-page_breadcrumb">
        <Crumb :data="pageTitleArr"/>
    </div>
    <div class="create-page_content">
        <div class="create-page_title">
            <div class="create-page_title-font">
                <SvgIcon icon-class="iconyijianfankui" size="17px"></SvgIcon>
                <span>{{pageName}}</span>
            </div>
        </div>
        <div class="create-page_body">
            <el-form :model="form" :rules="rules" ref="form" label-width="120px">
                <el-form-item label="标题:" prop="title">
                     <el-input v-model="form.title"  placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="发布者:" prop="promulgator">
                     <el-input v-model="form.promulgator"  placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="内容:" prop="content">
                    <editor 
                        upload-url="api"
                        v-model="form.content"
                        :input-content="introduction"/>
                </el-form-item>
                <el-form-item>
                    <div class="file-upload idea-upload">
                        <el-upload
                            class="upload-demo"
                            :action="uploadUrl"
                            :on-remove="handleRemove"
                            :before-remove="beforeRemove"
                            :on-success="handleSuccess"
                            :limit="1"
                            accept=".doc,.docx,.pdf"
                            :file-list="fileList"
                            :on-exceed="handleExceed">
                            <i class="el-icon-upload el-icon--right"></i>
                            <el-button type="text">上传附件</el-button>
                            <div slot="tip" class="el-upload__tip idea-tip">支持文件格式：.doc .docx .pdf ，单个文件不能超过20M</div>
                        </el-upload>
                    </div>
                </el-form-item> 
            </el-form>
            <div class="create-page_body-footer">
                <el-button @click="save('form')" type="primary">保存并发布</el-button>
            </div>
        </div>
    </div>
 </div>
</template>

<script>
import Editor from '../components/Editor';
import trainingProgramApi from '@/api/trainingProgramApi';
export default {
 name: 'IdeaCreate',
 components: {Editor},
 data() {
 return {
     pageName:'培养目标',
     pageTitleArr:[],
     type:this.$route.query.type,
     uploadUrl:'',
     fileList:[],
     titlebarShow:false,
     introduction:'',
     form: {
        title:'',
        promulgator:'',
        file:'',
        content:'',
        id:undefined
    },
    rules:{
        title:[
            {
              required: true,
              message: '请输入标题',
              trigger: 'blur'
            }
        ]
    },
 }
 },
 created(){
    this.pageName=this.$route.query.pageTitle[1]
    this.pageTitleArr=[this.$route.query.pageTitle[0],`${this.$route.query.pageTitle[1]}${this.$route.query.type === 'add' ? '新增' : '编辑'}`]
 },
 mounted(){
      this.uploadUrl=trainingProgramApi.upload({
        baseUrl:this.API_URL
     })
     if(this.type === 'edit'){
         this.initData();
     }
 },
 computed:{
     titleType(){
         return this.$route.query.type === 'add' ? '新增' : '编辑'
     }
 },
 methods:{
     initData(){
         const url =trainingProgramApi.conceptInfo
         this.$get(url).then(res =>{
             if(res.status === 1){               
                const newData= {
                    title:res.data.title,
                    promulgator:res.data.promulgator,                   
                    file:res.data.file || '',
                    content:res.data.content,
                    id:res.data.id
                } 
                  const fileArr=[{
                    name:res.data.fileName || '',
                    url:res.data.file || ''
                }]
                this.introduction=res.data.content
                this.fileList= res.data.file ? fileArr : []
                this.form=newData           
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         })
     },
      handleRemove(file, fileList) {
          this.form.file=''
      },
      handleSuccess(res){
          this.form.file=res.data.id
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
        if(files.size >20480){
              this.$message.warning(`亲！您上传的文件大小受限,请重新上`);
        }
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      },
     save(formName){
          this.$refs[formName].validate((valid) => {
          if (valid) {
                if(this.type === 'add'){
                     this.userAdd()      
                }else{
                    this.userEdit()
                }                      
          } 
        });
     },
     userAdd(){
        const url =trainingProgramApi.conceptAdd;
       this.$post(url,this.form)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message                           
                            })
                       setTimeout(() => {
                            this.$router.push('/trainingProgram/idea');      
                       }, 800);
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            })
     },
     userEdit(){
        const url =trainingProgramApi.conceptEdit;
        this.$put(url,this.form)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                         setTimeout(() => {
                            this.$router.push('/trainingProgram/idea');      
                       }, 800);
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            })
     }
 }
}
</script>

<style lang='scss'>
    @import '../../styles/create.scss';
    .idea-upload{
        text-align:left;
    }
    .idea-tip{
        display:inline-block;
    }
</style>



